import { Empty } from "antd";
import dayjs from "dayjs";
import React from "react";
import { useState } from "react";
import DataTable from "react-data-table-component";
import { AiFillEye } from "react-icons/ai";
import "./tabla.css";

const TablaPlanilla = ({ table, handleContrato, filas }) => {
  const [currentPage, setCurrentPage] = useState(0);

  const handlePageChange = (selected) => {
    setCurrentPage(selected); // actualiza el número de página actual al cambiar de página
  };
  const paginationComponentOptions = {
    rowsPerPageText: "Filas por página",
    rangeSeparatorText: "de",
    selectAllRowsItem: false,
    selectAllRowsItemText: "Todos",
  };

  const columns = [
    {
      id: "Nro",
      name: "Nro",
      width: "60px",
      selector: (row, index) => row?.id,
    },
    {
      id: "nombres",
      name: "Nombres y apellidos",
      selector: (row) => row?.nombre,
      width: "250px",
      sortable: true,
    },
    {
      id: "celular",
      name: "Celular",
      width: "150px",

      sortable: true,
      selector: (row) => (row?.telefono ? row.telefono : "---"),
      center: true,
    },
    {
      id: "fecha_inicio",
      name: "Fecha inicio",
      width: "120px",

      selector: (row) => row?.fecha_inicio,
    },
    {
      id: "fecha_fin",
      name: "Fecha fin",
      width: "150px",
      button: true,
      selector: (row) => row?.fecha_fin,
    },
    {
      id: "recomendado_por",
      name: "Recomendado por",
      width: "150px",

      button: true,
      selector: (row) =>
        row?.evaluacion?.at(-1)?.recomendado_por
          ? row?.evaluacion?.at(-1)?.recomendado_por
          : "---",
    },
    {
      id: "Dias",
      name: "Dias laborados",
      button: true,
      selector: (row) => (row?.asistencia ? row.asistencia : "--"),
      style: {
        borderBotton: "none",
        color: "#555555",
        // maxWidth: "40px",
      },
    },
    {
      id: "pago",
      name: "Pago",
      selector: (row) =>
        (row.volquete || 0) + " (Volq.) - " + (row?.teletran || 0) + " (Tele.)",

      center: true,
    },

    {
      id: "total",
      name: "Total",
      button: true,
      selector: (row) => (row?.total || 0) + " (Tele.)",

      center: true,
    },
    {
      id: "control",
      name: "Control",
      button: true,
      center: true,
      cell: (e) => (
        <>
          <AiFillEye
            onClick={() => handleContrato(e)}
            style={{ cursor: "pointer" }}
          />
        </>
      ),
    },
  ];

  const conditionalRowStyles = [
    {
      when: (row) => row.fecha_fin_estimada || row.fecha_fin,
      style: (row) => {
        let fechaFin = row.fecha_fin_estimada
          ? row.fecha_fin_estimada
          : row.fecha_fin;
        // Cambiar formato de fecha a mm-dd-yyyy
        fechaFin = fechaFin.split("-").reverse().join("-");
        const fechaFinDate = new Date(fechaFin);
        const now = new Date();
        // Asegurarse de que las fechas se comparen solo en términos de día, mes y año.
        const fechaFinSimple = new Date(
          fechaFinDate.getFullYear(),
          fechaFinDate.getMonth(),
          fechaFinDate.getDate()
        );
        const nowSimple = new Date(
          now.getFullYear(),
          now.getMonth(),
          now.getDate()
        );
        const differenceInTime = fechaFinSimple.getTime() - nowSimple.getTime();
        const differenceInDays = Math.ceil(
          differenceInTime / (1000 * 3600 * 24)
        );

        if (differenceInDays > 5) {
          return {}; // No cambies el color si la fecha está a más de 5 días de distancia
        } else if (differenceInDays <= 5 && differenceInDays > 3) {
          return { backgroundColor: "#D4E8CF" }; // Cambia a color verde si faltan 5 días o menos, pero más de 3 días
        } else if (differenceInDays <= 3 && differenceInDays >= 0) {
          return { backgroundColor: "#e8d2cf" }; // Cambia a color rojo si faltan 3 días o menos
        } else if (differenceInDays < 0) {
          return { backgroundColor: "#e8d2cf" }; // Cambia a color rojo si la fecha estimada ya ha pasado
        }
      },
    },
  ];

  return (
    <div className="table-container">
      <DataTable
        columns={columns}
        data={table}
        pagination
        striped
        highlightOnHover
        responsive
        paginationComponentOptions={paginationComponentOptions}
        paginationPerPage={filas}
        paginationRowsPerPageOptions={[
          filas,
          filas * 2,
          filas * 3,
          filas * 4,
          filas * 5,
        ]}
        onChangePage={handlePageChange}
        conditionalRowStyles={conditionalRowStyles}
        noDataComponent={
          <Empty
            image={Empty.PRESENTED_IMAGE_SIMPLE}
            description={<span>No hay registros para mostrar.</span>}
          />
        }
      />
    </div>
  );
};

export default TablaPlanilla;
